*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body{font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}ul{list-style:none;margin:0;padding:0}a{color:#615976;text-decoration:none}a:hover{opacity:0.8}button:hover{cursor:pointer;opacity:0.8}

.home{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.container__posts{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:95vw;min-height:80vh;margin:2rem auto}

header{width:100%;height:100px;background-color:#fff;padding:0 1.5rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;box-shadow:0px 4px 4px rgba(107,79,177,0.2)}.logo__header{display:-webkit-flex;display:flex;-webkit-align-items:baseline;align-items:baseline}.logo__text{display:none;color:#6B4FB1;font-style:bold;font-weight:900;font-size:60px;margin-left:5px;line-height:normal}.nav__container{height:70%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:flex-end;align-items:flex-end}.button__post{cursor:pointer;color:#fff;border-radius:5px;border:none;width:77px;height:36px;background:linear-gradient(90deg, #495DE0 0%, #6B4FB1 100%);font-family:"Poppins",sans-serif;font-style:normal;font-weight:600}.button__post:hover{opacity:0.9}.menu__button{color:#6B4FB1;font-size:1.4rem;transition:color 300ms}.menu__button:hover{color:#495DE0;cursor:pointer}.menu-item{margin-bottom:5%;font-size:1.2rem}.menu-item:hover{color:#495DE0}.dropdown{position:relative;display:inline-block}input[type=checkbox]{display:none}.dropdown-content{display:none;position:absolute;top:40px;right:-10px;background-color:#fff;box-shadow:-4px 4px 4px rgba(107,79,177,0.2);padding:12px 16px;text-align:right;z-index:1}input[type=checkbox]:checked ~ .dropdown-content{display:block}@media (min-width: 700px){.logo__text{display:block}.nav__container{-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.button__post{margin-right:3.8rem}.menu__button{font-size:1.8rem}.menu-item{font-size:1.4rem}.dropdown-content{top:60px}}

.container__card--post{width:340px;max-height:294px;background:#fff;box-shadow:0px 4px 30px rgba(44,31,78,0.2);border-radius:15px;color:#6B4FB1;padding:3rem 2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:2rem 4rem}.card__post--header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.card__post--header h3{font-weight:bold;font-size:18px}.card__post--content{color:#615976;font-size:1.4rem;width:100%;height:147px;margin:1rem 0}.card__post--actions{-webkit-align-self:flex-end;align-self:flex-end;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.card__post--actions button{background:none;border:none}.card__post--actions span{text-align:center;margin:0 .5rem;font-weight:600;font-size:18px}

.container__footer{background-color:#6B4FB1;width:100%;height:50px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;color:#fff;padding:1rem 1.5rem}.container__copy{width:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:small}.container__copy img{margin-right:5px}.container__copy p span:hover{cursor:pointer;color:#495DE0}.container__socialMedia{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:auto}.container__socialMedia li:nth-child(even){margin:0 5px}

.container__card--form{position:absolute;top:100px;left:0;right:0;bottom:0;margin:auto;width:340px;height:294px;background:linear-gradient(180deg, rgba(255,255,255,0.16) 0%, rgba(255,255,255,0.04) 100%);mix-blend-mode:normal;border:3px solid #495DE0;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:15px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:2rem 3rem}.card__btnClose{background:none;width:25px;height:auto;border:none;cursor:pointer;outline:none;-webkit-align-self:flex-end;align-self:flex-end}.card__btnClose:hover{-webkit-filter:opacity(0.8);filter:opacity(0.8)}.card__title{margin-top:1rem}.input-txt{background:none;outline:none;border:none;font-size:1.8rem}.card__contentPost{height:100px;width:100%;resize:none;margin:1.5rem 0;padding:1rem .5rem;font-family:Poppins;font-style:normal;font-weight:normal;font-size:14px;line-height:21px;background:none;border:2px solid #495DE0;border-radius:10px;outline:none}form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.card__btnPost{width:131px;height:34.17px;background:linear-gradient(270deg, #6B4FB1 0%, #475DE2 98.47%);box-shadow:0px 4px 10px 2px rgba(105,80,180,0.6);border-radius:30px;padding:.5rem 2rem;color:#fff;font-weight:600;font-size:14px;display:block;-webkit-align-self:center;align-self:center;text-align:center;cursor:pointer;margin-top:10px}.card__btnPost:hover{opacity:0.9}

.container__post--detail{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.back{-webkit-align-self:flex-start;align-self:flex-start;margin:2rem}.post__detail--comments h2{font-weight:600;font-size:24px;line-height:36px;color:#495DE0}

body{margin:0;font-family:"Poppins",sans-serif;background-color:#EFF3FC}html{font-size:62.5%}

